const ToDoListInput = React.createClass({
  getInitialState: function(){
		return {
			toDoItems: []
		}
  },

  _handleAddNewToDo: function(newItem){
    let toDoArrCopy = this.state.toDoList.map(function(copy){return copy})
    toDoArrCopy.push(newItem)
    this.setState({
      toDoList: toDoArrCopy
    })
  },

  render: function(){

    return(

      <div class="container">
        <h2>To Do List</h2>
        <form className="form-inline">
          <div className="form-group">
            <label for="toDoInput" class="sr-only"></label>
            <div className="col-md-12">
              <input type="" className="form-control" id="toDoInput" placeholder="New To Do Item"/>
              <button type="submit" className="btn btn-default" onClick={this._handleNewToDoInput}>
                <i className="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </form>
      </div>

    )
  }

})


